  }
  .services-footer .services-contact-us {
    width: 30%;
    padding: 8% 1% 8% 0%;
  }
  .services-footer .services-contact-us .services-contact-head {
    font-size: 1.5rem;
  }
  .services-footer .services-contact-us .services-contacts {
    margin-left: 5px;
  }
  .services-footer .services-contact-us .services-contacts p {
    font-size: 1rem;
  }
}
@media only screen and (max-width: 768px) {
  .services-display img {
    height: 40vh;
  }
  .services-display .display-color {
    height: 40vh;
  }
  .services-display p {
    font-size: 1.7rem;
  }
  .type-name .type-head-container {
    margin-bottom: 1.1rem;
  }
  .type-name .type-head-container .type-container {
    width: 85%;
  }
  .type-name .type-head-container .type-container .type-head {
    font-size: 2rem;
  }
  .type-name .type-head-container .type-container .type-subhead {
    font-size: 1.1rem;
  }
  .item {
    margin: 0 5px;
  }
  .services-typewriter .services-best {
    font-size: 0.97rem;
  }
  .services-typewriter .services-best span {
    font-size: 0.97rem;
  }
  .services-footer {
    display: block;
    font-size: 1rem;
  }
  .services-footer .services-footer-info {
    width: 100%;
    padding: 2% 5%;
  }
  .services-footer .services-footer-info .services-logo-image {
    margin-bottom: 0;
  }
  .services-footer .services-services {
    width: 100%;
    padding: 2% 5%;
  }
  .services-footer .services-services .services-services-head {
    margin-bottom: 20px;
  }
  .services-footer .services-services ul li {
    margin-bottom: 2px;
  }
  .services-footer .services-contact-us {
    width: 100%;
    padding: 1% 5% 2% 5%;
  }
  .services-footer .services-contact-us .services-contact-head {
    margin-bottom: 20px;
  }
  .services-footer .services-contact-us .services-contacts .services-phone {
    margin-left: 5px;
  }
  .rec.rec-arrow-left {
    margin-left: 5px;
  }
  .rec.rec-arrow-right {
    margin-right: 5px;
  }
}
@media only screen and (max-width: 562px) {
  .services-display img {
    height: 30vh;
  }
  .services-display .display-color {
    height: 30vh;
  }
  .services-display p {
    font-size: 1.4rem;
  }
  .type-name {
    padding-bottom: 1.5rem;
  }
  .type-name .type-head-container {
    margin-bottom: 0.5rem;
  }
  .type-name .type-head-container .type-container {
    width: 95%;
  }
  .type-name .type-head-container .type-container .type-head {
    font-size: 1.5rem;
  }
  .type-name .type-head-container .type-container .type-subhead {
    font-size: 0.8rem;
  }
  .item {
    height: 25vh;
    margin: 0;
  }
  .services-typewriter .services-best {
    font-size: 0.6rem;
  }
  .services-typewriter .services-best span {
    font-size: 0.6rem;
    margin-left: 1px;
  }
  .services-footer .services-footer-info {
    padding: 1% 5%;
  }
  .services-footer .services-footer-info .services-logo-image {
    margin-bottom: 0;
  }
  .services-footer .services-services .services-services-head {
    margin-bottom: 20px;
  }
  .services-footer .services-contact-us .services-contact-head {
