div#slider {
  overflow: hidden;
}
div#slider figure {
  position: relative;
  width: 500%;
  margin: 0;
  left: 0;
  text-align: left;
  font-size: 0;
  -webkit-animation: 10s slidy infinite;
          animation: 10s slidy infinite;
  opacity: 0.7;
}
div#slider figure img {
  width: 20%;
  float: left;
  height: 100vh;
}
div#slider .text {
  position: absolute;
  top: 50%;
  text-align: center;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  color: rgb(24, 24, 24);
  font-family: "Lato", sans-serif;
  font-weight: 700;
}
div#slider .small-text {
  position: absolute;
