  font-weight: 700;
  color: rgb(62, 61, 61);
}
div#slider .btn {
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.3rem;
  letter-spacing: 0.1rem;
  padding: 0.6% 1%;
  border-radius: 5px;
  background-color: rgb(4, 4, 49);
  color: white;
  box-shadow: 2px 2px 10px 5px grey;
  cursor: pointer;
}
div#slider .btn:hover {
