.nav {
  display: flex;
  padding: 0.5rem;
  background-color: rgb(4, 4, 49);
  color: white;
  font-size: 1.1rem;
  position: relative;
}
.nav .logo {
  display: flex;
  width: 33%;
  padding: 0.8rem;
}
.nav .logo #logo-image {
  width: 50px;
  position: absolute;
  top: 15%;
  left: 1%;
}
.nav .logo a {
  text-decoration: none;
  color: white;
}
.nav .logo a span {
  margin-left: 52px;
  letter-spacing: 0.15rem;
  word-spacing: 0.3rem;
}
.nav .others {
  width: 67%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  letter-spacing: 0.1rem;
}
.nav .others ul {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav .others ul li a {
  color: white;
  text-align: center;
  text-decoration: none;
  padding: 0.3rem;
  margin: 0 1rem;
  border-radius: 5px;
}
.nav .others ul li a:hover {
  border-bottom: 1px solid white;
}
.nav .others ul li .nav-dropdown {
  overflow: hidden;
  cursor: pointer;
  border-radius: 5px;
  padding: 0.3rem;
  margin: 0 1rem;
}
.nav .others ul li .nav-dropdown:hover {
  border-bottom: 1px solid white;
}
.nav .others ul li .nav-dropdown .nav-dropbtn {
  font-size: 1.1rem;
  color: white;
  border-radius: 5px;
  border: none;
  outline: none;
  background-color: inherit;
  font-family: inherit;
  cursor: pointer;
}
.nav .others ul li .nav-dropdown .nav-dropbtn .arrow {
  margin-left: 3px;
  font-size: 15px;
}
.nav .others ul li .nav-dropdown .nav-dropdown-content {
  font-size: 1.1rem;
  display: block;
  position: absolute;
  top: 100%;
  right: 15%;
  background-color: #f9f9f9;
  width: 250px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.nav .others ul li .nav-dropdown .nav-dropdown-content a {
  color: black;
  text-decoration: none;
  display: block;
  text-align: center;
  padding: 0.5rem;
  margin: 0;
}
.nav .others ul li .nav-dropdown .nav-dropdown-content a:hover {
  background-color: #ddd;
}
.nav .others-display-list ul {
  display: none;
}
.nav .nav-bars {
  display: none;
}

@media only screen and (max-width: 1140px) {
  .nav {
    font-size: 1rem;
  }
  .nav .logo {
    width: 50%;
  }
  .nav .logo #logo-image {
    left: 0.5%;
  }
  .nav .logo a span {
    margin-left: 30px;
    word-spacing: 0.2rem;
  }
  .nav .others {
    padding-left: 0%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  .nav .others ul li a {
    margin: 0 0.3rem;
    padding: 0.1rem;
  }
  .nav .others ul li .nav-dropdown {
    padding: 0.1rem;
    margin: 0 0.3rem;
  }
  .nav .others ul li .nav-dropdown .nav-dropbtn {
    font-size: 1rem;
  }
  .nav .others ul li .nav-dropdown .nav-dropbtn .arrow {
    margin-left: 1px;
    font-size: 11px;
  }
  .nav .others ul li .nav-dropdown .nav-dropdown-content {
    font-size: 1rem;
    right: 5%;
    width: 210px;
  }
  .nav .others ul li .nav-dropdown .nav-dropdown-content a {
    padding: 0.2rem 0.5rem;
  }
  .nav .others-display-list ul {
    display: none;
  }
}
@media only screen and (max-width: 768px) {
  .nav {
    font-size: 1rem;
  }
  .nav .logo {
    width: 100%;
  }
  .nav .others {
    display: none;
  }
  .nav .others-display-list ul {
    list-style: none;
    display: inline;
    background-color: rgb(4, 4, 49);
    position: absolute;
    top: 100%;
    right: 0%;
    z-index: 1;
    border-top-right-radius: 0;
    cursor: pointer;
  }
  .nav .others-display-list ul li {
    padding: 0.5rem;
    margin: 0;
  }
  .nav .others-display-list ul li:hover {
    background-color: rgb(8, 8, 86);
  }
  .nav .others-display-list ul li a {
    color: whitesmoke;
    text-align: center;
    text-decoration: none;
  }
  .nav .others-display-list ul li a:hover {
    border-bottom: none;
  }
  .nav .others-display-list ul li .nav-dropdown {
    margin: 0;
  }
  .nav .others-display-list ul li .nav-dropdown:hover {
    border-bottom: none;
  }
  .nav .others-display-list ul li .nav-dropdown .nav-dropbtn {
    font-size: 1rem;
    color: whitesmoke;
    padding: 0.1rem 0.3rem;
    border: none;
    outline: none;
    background-color: inherit;
    padding: 0;
    cursor: pointer;
  }
  .nav .others-display-list ul li .nav-dropdown .nav-dropbtn .arrow {
    font-size: 11px;
  }
  .nav .others-display-list ul li .nav-dropdown .nav-dropdown-content {
    font-size: 1.1rem;
    display: block;
    position: absolute;
    top: 100%;
    right: 0%;
    background-color: #f9f9f9;
    width: 200px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
  }
  .nav .others-display-list ul li .nav-dropdown .nav-dropdown-content a {
    padding: 0.2rem 0.2rem;
    color: black;
    text-decoration: none;
    display: block;
    text-align: center;
    margin: 0;
  }
  .nav .others-display-list ul li .nav-dropdown .nav-dropdown-content a:hover {
    background-color: #ddd;
  }
  .nav .nav-bars {
    width: 10%;
    display: block;
    position: absolute;
    right: 2%;
    top: 35%;
    font-size: 20px;
    cursor: pointer;
    background-color: inherit;
    color: white;
    border: none;
    outline: none;
  }
}
@media only screen and (max-width: 562px) {
  .nav {
    font-size: 0.9rem;
  }
  .nav .logo {
    width: 600%;
    padding: 0.8rem 0.5rem;
  }
  .nav .logo #logo-image {
    top: 13%;
    width: 45px;
  }
  .nav .others-display-list ul li {
    padding: 0.2rem 0.1rem;
  }
  .nav .others-display-list ul li a {
    font-size: 0.9rem;
  }
  .nav .others-display-list ul li .nav-dropdown .nav-dropbtn {
    font-size: 1rem;
    padding: 0;
  }
  .nav .others-display-list ul li .nav-dropdown .nav-dropdown-content {
    font-size: 0.9rem;
    width: 100px;
  }
  .nav .others-display-list ul li .nav-dropdown .nav-dropdown-content a {
    font-size: 0.9rem;
  }
}/*# sourceMappingURL=Navbar.css.map */