  color: #0C2D48;
}
.gallery-container .display .img-1 {
  position: absolute;
  border-radius: 50%;
  border: 6px solid #90ADC6;
}
.gallery-container .display .img-1 img {
  padding: 10px;
  width: 14vw;
}
.gallery-container .display .img-4 {
  position: absolute;
  border-radius: 50%;
  border: 6px solid #90ADC6;
  left: 35%;
  top: 10%;
}
.gallery-container .display .img-4 img {
  padding: 17px;
  width: 10vw;
}
.gallery-container .display .img-3 {
  position: absolute;
  border-radius: 50%;
  border: 6px solid #90ADC6;
  left: 15%;
  top: 70%;
}
.gallery-container .display .img-3 img {
  padding: 20px;
  width: 10vw;
}
.gallery-container .display .img-2 {
  position: absolute;
  border-radius: 50%;
  border: 6px solid #90ADC6;
  left: 63%;
  top: 10%;
}
.gallery-container .display .img-2 img {
  padding: 20px;
  width: 14vw;
}
.gallery-container .display .outerRect {
  border: 20px solid #90ADC6;
  position: absolute;
  width: 35vw;
  height: 67vh;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0%);
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
}
.gallery-container .display .outerRect .innerRect {
  position: absolute;
  width: 32.5vw;
  height: auto;
  bottom: -52%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  overflow: hidden;
}
.gallery-container .display .outerRect .innerRect figure {
  position: relative;
  width: 500%;
  margin: 0;
  left: 0;
  -webkit-animation: 10s slidy infinite;
          animation: 10s slidy infinite;
}
.gallery-container .display .outerRect .innerRect figure img {
  width: 32.5vw;
  height: 62.5vh;
}
.gallery-container .display .consultant-form {
  position: absolute;
  top: 30%;
  right: 1.5%;
  background-color: white;
  width: 27%;
  padding: 30px;
  border-radius: 20px;
}
.gallery-container .display .consultant-form .form-head {
  text-align: center;
  padding: 10px;
  margin-bottom: 20px;
  font-size: 1.3rem;
}
.gallery-container .display .consultant-form .form-head h3 {
  padding: 5px;
  display: inline;
  border-radius: 5px;
  border-bottom: 3px solid darkblue;
}
.gallery-container .display .consultant-form input {
  width: 100%;
  padding: 10px 15px;
  margin-bottom: 15px;
  border: none;
  border-radius: 10px;
  font-size: 1rem;
  box-shadow: 1px 1px 5px 1px grey;
}
.gallery-container .display .consultant-form .submit {
  margin-top: 15px;
  text-align: center;
}
.gallery-container .display .consultant-form .submit input {
  background-color: #05445E;
  color: whitesmoke;
  font-size: 1.2rem;
  cursor: pointer;
}
.gallery-container .display .consultant-form .submit input:hover {
  background-color: #085f84;
}

@-webkit-keyframes slidy {
  0% {
    left: 0%;
  }
  20% {
    left: 0%;
  }
  25% {
    left: -100%;
  }
  45% {
    left: -100%;
  }
  50% {
    left: -200%;
  }
  70% {
    left: -200%;
  }
  75% {
    left: -300%;
  }
  95% {
    left: -300%;
  }
  100% {
    left: -400%;
  }
}

@keyframes slidy {
  0% {
    left: 0%;
  }
  20% {
    left: 0%;
  }
  25% {
    left: -100%;
  }
  45% {
    left: -100%;
  }
  50% {
    left: -200%;
  }
  70% {
    left: -200%;
  }
  75% {
    left: -300%;
  }
  95% {
    left: -300%;
  }
  100% {
    left: -400%;
  }
}
